import styled from 'styled-components';

export const FullScreenDiv = styled.div`
    width: 100%;
    height: calc( 100vh - 100px );
    margin: 0 0 50px 0;
    display: block;
`;

export const FullScreenDivHead = styled.h2`
    color: gray;
    padding: 20px;
    text-align: center;
`;
